import Cookies from 'js-cookie';
import { IAction } from '../actions';
import { SET_USER, TOGGLE_ASIDE } from '../constants/app';

export type User = {
  name: string,
}

export interface IAppState {
  user: User,
  collapsed: boolean;
}

// defaultState
const initialState = {
  user: {
    name: 'sobird'
  },
  collapsed: Cookies.get(TOGGLE_ASIDE) === '1',
};

export default function (state: IAppState = initialState, action: IAction) {
  switch (action.type) {
    case SET_USER :
      return {
        ...state,
        user: action.payload,
      };
    case 'TOGGLE_ASIDE':
      if (state.collapsed) {
        Cookies.set(TOGGLE_ASIDE, '0', {
          path: '/',
        });
      } else {
        Cookies.set(TOGGLE_ASIDE, '1', {
          path: '/',
        });
      }
  
      // window.resize
      setTimeout(() => {
        window.dispatchEvent(new Event('resize'));
      }, 300);
  
      return {
        ...state,
        collapsed: !state.collapsed,
      };
    default:
      return state;
  }
}